import React, { Component } from 'react';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css';
import Grid from '@material-ui/core/Grid';

class Draftboxformsmall extends Component {
  constructor(props) {
    super(props);
    this.state = {
      authorName: '',
      phone: '',
      email: '',
      title: '',
      content: '',
      agreement: '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(e) {
    this.setState({ [e.target.id]: e.target.value });
  }

  handleSubmit(e) {
    e.preventDefault();
    confirmAlert({
      title: 'Confirm',
      message: '   ?',
      buttons: [
        {
          label: '',
          onClick: () => {
            this.props.onCreate(this.state);

            this.setState({
              authorName: '',
              phone: '',
              email: '',
              title: '',
              content: '',
              agreement: '',
            });
          },
        },
        {
          label: '',
          onClick: () => {
            return;
          },
        },
      ],
    });
  }

  render() {
    return (
      <Grid container item xs={12}>
        <form onSubmit={this.handleSubmit}>
          <Grid item className="h2box" xs={12}>
            <h2 className="tkdeka">   </h2>
          </Grid>

          <Grid item xs={12}>
            <input
              className="fullformsmall"
              placeholder=""
              value={this.state.authorName}
              onChange={this.handleChange}
              id="authorName"
            />
          </Grid>

          <Grid item xs={12}>
            <input
              className="fullformsmall"
              placeholder=""
              value={this.state.phone}
              onChange={this.handleChange}
              id="phone"
            />
          </Grid>

          <Grid item xs={12}>
            <input
              className="fullformsmall"
              placeholder=""
              value={this.state.email}
              onChange={this.handleChange}
              id="email"
            />
          </Grid>

          <Grid item xs={12}>
            <input
              className="fullformsmall"
              placeholder="     "
              value={this.state.title}
              onChange={this.handleChange}
              id="title"
            />
          </Grid>

          <Grid item xs={12}>
            <textarea
              className="ansdmlsodydsmall"
              placeholder="  (////  )"
              value={this.state.content}
              onChange={this.handleChange}
              id="content"
            />
          </Grid>

          <Grid item xs={12} className="tnwlq">
                  .
                    
             ,       
                    
            .          
              ,        
                .  
                  
             .
          </Grid>
          <Grid container>
            <Grid item xs={12} align="end">
              
              <input type="checkbox" onChange={this.handleChange} />
            </Grid>
          </Grid>
          <Grid item xs={12} className="fullformsmall">
            <button className="fullformsmall" type="submit">
              
            </button>
          </Grid>
        </form>
      </Grid>
    );
  }
}

export default Draftboxformsmall;
